import { Tabbar } from 'react-vant'
import { AttrList } from '../select'
import { FriendsO, HomeO, Search, SettingO } from '@react-vant/icons'

const attr = []

const MyTabBar = props => {
  const { property } = props || {}
  return (
    <>
      {property ? (
        AttrList(attr, props)
      ) : (
        <Tabbar>
          <Tabbar.Item icon={<HomeO />}></Tabbar.Item>
          <Tabbar.Item icon={<Search />}></Tabbar.Item>
          <Tabbar.Item icon={<FriendsO />}></Tabbar.Item>
          <Tabbar.Item icon={<SettingO />}></Tabbar.Item>
        </Tabbar>
      )}
    </>
  )
}

export default MyTabBar
